body {
  --black: #000000;
  --ash-black: #222;
  --white: #fafafa;
  --sky: #00ccff;
  --green: #22dddd;
  --blue: #1300ff;
  --dusk: #6600ff;
  --purple: #9900ff;
  --pinkk: #22dddd;
  --pink: #ff00a6;
  --red: #fe0222;
  --orange: #fd7702;
  --yellow: #ffbb00;

  --background: var(--black);
  --accent: var(--pink);

  background-color: var(--background);
  overflow-x: hidden;
  padding-bottom: 100px;
  /* text-shadow: 2px 2px #663737; */
}

* {
  font-family: sofia-pro, sans-serif;
  font-weight: 400;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
}

h1,
h2,
h3 {
  font-family: sofia-pro, sans-serif;
  font-weight: 600;
  font-style: normal;
  color: #fafafa;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-top: 1px dotted var(--accent);

  background-image: radial-gradient(
    circle at 50% 50%,
    var(--background) 20%,
    transparent 20.5%,
    transparent 49.5%,
    var(--background) 50%
  );

  background-size: 15px 15px;
  backdrop-filter: blur(20px) brightness(30%);
  -webkit-backdrop-filter: blur(20px) brightness(30%);
  -moz-backdrop-filter: blur(20px) brightness(30%);
  font-size: 14px;
  line-height: 14px;
}

footer a {
  text-decoration: none;
  color: var(--accent);
}

code {
  font-family: input-mono, monospace;
  font-weight: 400;
  font-style: normal;
}

circle {
  stroke-dashoffset: 0;
  stroke-width: 15%;
  fill: none;
}

.bg {
  stroke: var(--accent);
  opacity: 0.3;
}

.box {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background: #ffffff;
}

/*moving text */
.moving-text-container {
  position: relative;
  width: 100vw;
}

.moving-text {
  position: absolute;
  white-space: nowrap;
  font-size: 200px;
  font-weight: bold;
  color: transparent;
  -webkit-text-stroke: 5px white;

  animation: move-text 50s ease-in-out infinite;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fafafa;
  text-align: center;
}

.stack-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: #fafafa;
  text-align: center;
}

.container {
  width: 90vw;
}

.Banner-section {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
}

.Skill-section {
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  color: #fafafa;
  white-space: nowrap;
}

.skill-item {
  display: inline-block;
  width: 20%;
  text-align: center;
  animation: skill-text 10s ease-in-out infinite;
}

.skill_icon {
  display: flex;
  flex-direction: column;
}
.progress {
  position: fixed;
  left: 0;
  right: 0;
  height: 5px;
  background: var(--accent);
  bottom: 55px;
}
@keyframes skill-text {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-200%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes move-text {
  0% {
    transform: translateX(0%);
  }
  50% {
    transform: translateX(-80%);
  }
  100% {
    transform: translateX(0%);
  }
}
h1 {
  text-shadow: 2px 2px #663737;
}
.project {
  color: #ffffff;
  opacity: 0;

  transform: translateX(100px);
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
  text-align: center;
}

.project.hovered {
  opacity: 1;
  transform: translateX(0);
}

::-webkit-scrollbar {
  display: none;
}

/*klksl*/

.projects-item > img {
  max-height: 400px;
  max-width: 500px;
  border-radius: 2%;
}

img {
  filter: grayscale(60%);
  transition: filter 0.5s ease;
  border: 2px solid var(--accent);
}

img:hover {
  filter: grayscale(0%);
  scale: 1.01;
}

form {
  margin: 0;
}
.form-container {
  max-width: 700px;
}
form input,
form textarea {
  width: 100%;
  border: 0;
  outline: none;
  background: #262626;
  padding: 15px;
  margin: 15px 0;
  font-size: 18px;
  border-radius: 6px;
}
@media screen and (max-width: 600px) {
  .projects-item > img {
    max-height: 250px;
    max-width: 250px;
    border-radius: 1%;
  }
  img {
    filter: grayscale(100%);
    transition: filter 0.5s ease;
    border: 2px solid var(--accent);
  }
}
